import { createSlice, PayloadAction } from '@reduxjs/toolkit';
import { CardData } from '../api/api-slice';

const initialState = {} as CardData

const selectCardSlice = createSlice({
  name: 'select-card',
  initialState,
  reducers: {
    selectedCard(state, { payload }: PayloadAction<CardData>) {
      return payload
    }
  }
})

export default selectCardSlice.reducer;
export const { selectedCard } = selectCardSlice.actions