import { createSlice, PayloadAction } from '@reduxjs/toolkit';
import { UserData, UserDetails } from '../api/api-slice'

const initialState: UserData = {} as UserData;

const currentUserSlice = createSlice({
  name: 'user',
  initialState,
  reducers: {
    userFetched(state, { payload }: PayloadAction<UserData>) {
      return payload
    },
    profileInfoEdited(state, action: PayloadAction<UserDetails>) {
      state.name = action.payload.name;
      state.about = action.payload.about;
    },
    avatarChanged(state, action: PayloadAction<string>) {
      state.avatar = action.payload
    },
  },
});

export const { userFetched, profileInfoEdited, avatarChanged } = currentUserSlice.actions;
export default currentUserSlice.reducer;





