import PopupWithForm from "../../PopupWithForm";
import {
  useAddCardMutation,
} from "../../../../../../features/api/api-slice";
import { ChangeEvent, SyntheticEvent, useEffect, useRef } from "react";
import {
  PopupName,
  toggledAddCardPopupVisibility,
} from "../../../../../../features/popups/popups-visibility-slice";
import { useAppDispatch, useAppSelector } from "../../../../../App/hooks";
import { InputName, InputValues, useForm } from "../../../../hooks-popup";

function AddCardPopup() {

  const { values, handleChange, setValues }= useForm({} as InputValues);

  const isOpen = useAppSelector(
    (state) => state.popupsVisibility.addCardPopupIsVisible
  );
  const dispatch = useAppDispatch();
  const [addCard, { isLoading: addCardIsLoading, isSuccess: addCardIsSuccess, reset }] = useAddCardMutation();

  useEffect(() => {
    // mutation is reset onSubmit => isSuccess becomes false
    if (addCardIsSuccess) {
      dispatch(toggledAddCardPopupVisibility());
      setValues({
        ...values,
        [InputName.cardName]: '',
        [InputName.cardLink]: '',
      });
    }
  }, [addCardIsSuccess]);

  return (
    <PopupWithForm
      name={PopupName.addCard}
      isOpen={isOpen}
      title="New place"
      onClose={() => dispatch(toggledAddCardPopupVisibility())}
      onSubmit={(e) => {
        e.preventDefault();
        reset();
        addCard({ name: values[InputName.cardName], link: values[InputName.cardLink] });
      }}
      buttonText={addCardIsLoading ? "Saving..." : "Create"}
    >
      <div className="form__input-container">
        <input
          type="text"
          maxLength={30}
          id="input-cardName"
          name={InputName.cardName}
          placeholder="Name"
          className="form__input form__input_valid form__input_type_title"
          onChange={handleChange}
          value={values[InputName.cardName] || ''}
          required
        />
        <span className="form__error form__error_type_title">.</span>
      </div>
      <div className="form__input-container">
        <input
          type="url"
          id="input-cardLink"
          name={InputName.cardLink}
          placeholder="Image URL"
          className="form__input form__input_valid form__input_type_url"
          onChange={handleChange}
          value={values[InputName.cardLink] || ''}
          required
        />
        <span className="form__error form__error_type_url">.</span>
      </div>
    </PopupWithForm>
  );
}

export default AddCardPopup;
