import { ReactNode, useEffect, MouseEvent } from 'react'
import { PopupName } from '../../features/popups/popups-visibility-slice';

interface popupOptions {
  classes: string
}

interface popupProps {
  children?: ReactNode;
  name: PopupName;
  isOpen: boolean;
  onClose: () => void;
  options?: popupOptions
}

function Popup({ children, name, isOpen, onClose, options} : popupProps ) {
  
  useEffect(() => {
    if (!isOpen) return
    const closeOnEsc = (e: KeyboardEvent) => {
      if (e.key === 'Escape') {
        onClose();
      }
    };

    document.addEventListener('keydown', closeOnEsc)
    return () => document.removeEventListener('keydown', closeOnEsc)
  }, [isOpen, onClose])

  const handleOverlay = (e: MouseEvent<HTMLDivElement>) => {
    if (e.target === e.currentTarget) {
      onClose()
    }
  }

  return (
    <div
      className={`popup popup_type_${name} ${isOpen ? 'popup_visible' : ''}`}
      onClick={handleOverlay}
    >
      <div
        className={`popup__window popup__window_type_${name} ${options && options.classes ? options.classes : ''}`}
      >
        <button
          type='button'
          className='popup__close-button'
          onClick={onClose}
        ></button>
        {children}
      </div>
    </div>
  );
}

export default Popup;
export type { popupProps };