import { useState, useMemo, ChangeEvent } from 'react';
import { createSelector } from '@reduxjs/toolkit';
import {
  useGetUserQuery,
  UserData,
  UserDetails,
} from '../../features/api/api-slice';
import { useAppSelector } from '../App/hooks';

const enum InputName {
  profileName = 'profileName',
  profileAbout = 'profileAbout',
  cardName = 'cardName',
  cardLink = 'cardLink',
  avatarLink = 'avatarLink'
}

type InputValues = { [key in InputName]: string }

const useForm = (inputValues: InputValues ) => {
  const [values, setValues] = useState(inputValues);
// 
  function handleChange(event: ChangeEvent<HTMLInputElement>) {
    if (event.target) {
    const { value, name } = event.target;
    setValues({ ...values, [name]: value });
  } else { throw new Error ('Sorry')}
  };

  return { values, handleChange, setValues };
};

const useUserDetails = () => {
  const selectUserDetails = useMemo(
    () =>
      createSelector(
        [(data: UserData) => data.name, (data: UserData) => data.about],
        (name, about) => ({ name: name, about: about })
      ),
    []
  );
  const { userDetails = {} as UserDetails, isSuccess } = useGetUserQuery(
    undefined,
    {
      selectFromResult: ({ data, isSuccess }) => ({
        userDetails: data ? selectUserDetails(data) : undefined,
        isSuccess,
      }),
    }
  );
  return { userDetails, isSuccess };
};

