import {
  CardData,
  useGetCardsQuery,
  useGetUserQuery,
} from '../../features/api/api-slice';
import { ReactNode } from 'react'
import Card from '../Card/Card';
import GalletySkeleton from './GallerySkeleton/GallerySkeleton';

export const Gallery = ({ children } : {children: ReactNode}) => {
  const {
    data: cards = [] as CardData[],
    isLoading: cardsAreLoading,
    isError: cardsError,
    isSuccess: cardsSuccess,
  } = useGetCardsQuery();

  const {
    userId,
    error: userIdError,
    isLoading: userIdIsLoading,
  } = useGetUserQuery(undefined, {
    selectFromResult: ({ data, error, isLoading }) => ({
      userId: data?._id,
      error,
      isLoading,
    }),
  });

  return (
    <section className='gallery'>
      {cardsAreLoading || userIdIsLoading ? (
        <GalletySkeleton length={6} />
      ) : cardsError || userIdError ? (
        <p>Oh no, there was an error</p>
      ) : cardsSuccess && userId ? (
        <>
          <ul className='gallery__cards'>
            {cards.map((cardData) => {
              return (
                <Card
                  cardData={cardData}
                  currentUserId={userId}
                  key={cardData._id}
                />
              );
            })}
          </ul>
          {children}
        </>
      ) : null}
    </section>
  );
};
