import { createSlice, isRejectedWithValue, Middleware, MiddlewareAPI, PayloadAction  } from "@reduxjs/toolkit"
import { create } from "domain"
import { toggledErrorPopupVisibility } from "../popups/popups-visibility-slice";

const errorHandler: Middleware = (api: MiddlewareAPI) => (next) => (action) => {
  if (isRejectedWithValue(action)) {
    api.dispatch(setError(action));
    api.dispatch(toggledErrorPopupVisibility());
  }
  return next(action);
};


interface Error {
  [key: string]: any;
  error: {
    [key: string]: any;
    message: string;
  };
  payload: {
    [key: string]: any;
    data: {
      [key: string]: any;
      _message: string;
      errors: Object;
      message: string;
      name: string;
    };
    message: string;
    status: string;
  };
}

const initialState = {} as Error

const errorSlice = createSlice({
  name: 'error',
  initialState,
  reducers: {
    setError(state, action: PayloadAction<Error>) {
      state.error = action.payload.error
      state.payload = action.payload.payload
    }
  }
})

export const { setError } = errorSlice.actions;
export default errorSlice.reducer;
export { errorHandler }
